* {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
body {
  padding: 60px;
}

input {
  width: 100%;
  font-size: 14pt;
  height: 2em;
  padding: 5px;
  border-radius: 10px;
  border: 1px solid grey;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 10px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10pc;
  height: 200px;
}

.card  img {
  max-height: 70%;
}

.card p {
  padding: 5px;
  text-align: center;
}